import './App.css';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container'
import {useState} from 'react'

function App() {
  ///state variables///
  const [input, setInput] = useState('')
  const [output, setOutput] = useState('0')
  const [permitDot, setPermitDot] = useState(true) //true is permission
  const [chainCalculation, setChainCalculation] = useState(false)
  const [lowerDisplayIsNull, setLowerDisplayIsNull] = useState(true)

  ///reusable functions for click handlers, hide and abstract as much complexity up here as possible to keep the code below easy to understand.
  const lastChar = () => input.slice(-1) // return last char.
  const lastTwoChars = () => input.slice(-2) //slice last 2 whenever you need
  const anOperatorPresent = (char) => /[+*\/-]/g.test(char) // test for 1 operator, needs lastChar() as argument when calling.
  const twoOperatorsPresent = (chars) => /[+*-/]{2}/g.test(chars) //test if there are two operators present, needs lastTwoChars() as argument when calling!!
  const onlyAppendInput = (pressedKey) => {
    setInput((input) => input + pressedKey)}
  const appendInputDotAllowed = (pressedKey) => {
    setInput((input) => input + pressedKey) //go ahead and set input
    setPermitDot(true)} // allow permission for further dots.
  const appendInputDotNotAllowed = (pressedKey) => {
    setInput((input) => input + pressedKey) //go ahead and set input
    setPermitDot(false)} // no permission for further dots.
  const replaceOperatorDotAllowed = (pressedKey) => {
    setInput((input) => input.slice(0, -1) + pressedKey) // replace last char
    setPermitDot(true)} // allow permission for further dots.
  //use long function names describing exactly what the function does. Even a noncoder should be able to understand part of your code.
  const replaceTwoOperatorsDotAllowed = (pressedKey) => {
    setInput((input) => input.slice(0, -2) + pressedKey) // replace last 2chars
    setPermitDot(true)} // allow permission for further dots.
  const appendZeroDotandDotNotAllowed = (pressedKey) => {
    setInput((input) => input + '0' + pressedKey) // add 0. instead of just .
    setPermitDot(false)} // no permission for further dots.
  const sanitizedInput = () => {
    let result = input.replace(/[^+*-/\d]/g, '') //remove NON^ operator-digit 
    return result.replace(/[-]{2}/g, '+')} // double - becomes + to avoid eval crash
  ///end of reusable functions, you will only have to write these once///

  
  ///start of button handler functions
  const handleNumber = (pressedKey) => {
    input === "0" && pressedKey === "0" ? setInput("0") // avoid zero stacking for top display.
    :onlyAppendInput(pressedKey) //numbers should not affect dotPermission, they are handled by AC, operator and dot handlers.
    ////-----------lower display--------------////
    lowerDisplayIsNull ? setOutput(pressedKey) //if screen is null just replace
    : input === "0" && pressedKey === "0" ? setOutput(pressedKey) // make sure to avoid zero stacking in bottom display
    : anOperatorPresent(lastChar()) ? setOutput(pressedKey) // if the screen currently holds an operator, replace with the new number.
    : setOutput(() => output + pressedKey) // else just add to current number.
    setLowerDisplayIsNull(false) // make sure null state is set to false now
  }

  const handleMinus = (pressedKey) => {
    if (twoOperatorsPresent(lastTwoChars())) { //if 2 ops in last 2 chars->
      console.log('too many operators found')
    } else { // if no such problem
      appendInputDotAllowed(pressedKey) //append the minus and allow dots.
    }////-----------lower display--------------////
    setOutput(pressedKey)
  } 

  const handleDot = (pressedKey) => {
    if (lastChar(pressedKey) === '.') {
      console.log('cannot have two adjacent dots')
    } else if (anOperatorPresent(lastChar()) || input === '') {
      appendZeroDotandDotNotAllowed(pressedKey)
      setOutput('0.') ///---set lower display---///
      setLowerDisplayIsNull(false) // set this to false screen no longer null
    } else if (permitDot === true) {
      appendInputDotNotAllowed(pressedKey)
      setOutput(() => output + pressedKey) ///---set lower display---///
    }
  }

  const handleOperator = (pressedKey) => {
    if (twoOperatorsPresent(lastTwoChars())) { //if return value is true
      replaceTwoOperatorsDotAllowed(pressedKey)
    } else if (lastChar() === '') { //if there is no char input yet
      console.log('no numerical input yet') //don't insert an operator
    } else if (anOperatorPresent(lastChar())) { 
      replaceOperatorDotAllowed(pressedKey)
    } else {
      appendInputDotAllowed(pressedKey) //append the minus and allow dots.
    }////-----------lower display--------------////
    setOutput(pressedKey)
  }

  const handleAC = () => {
    setInput('') //reset the input state
    setPermitDot(true) // make sure to reset dot permission on AC
    setOutput('0') // clear the output console
    setChainCalculation(false) //no longer a chain calculation
    setLowerDisplayIsNull(true) // null the bottom screen
  }

  const evaluate = () => {
    setOutput(eval(sanitizedInput())) // set output to evaluated sanitizedInput
    setChainCalculation(true) // allow for possible chain calculation
  }
    
  const continueOrRestart = (keyPress) => {
    if (keyPress === '.') { //we have to list the desired state changes in here for now to have guarantee of execution in this particular order. (as opposed to referring to two different functions each containing state changes.) that is why in this particular block we will only call setState functions separately in order and not refer to any outside functions
      setInput('0.') // new start value 
      setPermitDot(false) //dot already used so not permitted rn
      setOutput('0.') //set output
      setChainCalculation(false) //always set false after this function runs once
    } else if (anOperatorPresent(keyPress)) {
      setInput(output + keyPress) //sum result becomes new input + append operator
      setOutput(keyPress) // operator goes in bottom screen (value has gone up)
      setPermitDot(true) // dot permitted, could be followed by 0. input
      setChainCalculation(false) // again we only want to run this func once
    } else { //in case a digit is entered
      setInput(keyPress) //start with the new digit
      setPermitDot(true) //dot is allowed at this point
      setOutput(keyPress) // reset the output for new sum
      setChainCalculation(false) //set to false in all cases, not to keep calling this same function again, even if it is a chain calculation set to false.
    }
  }

  return (
    <div className="App">
      <Container className="App">

      <Row className="Sl Sl1"><Col xs={12}>{input}</Col></Row>
      <Row className="Sl Sl2"><Col xs={12} id="display">{output}</Col></Row>

      <Row className="El">
        <Col xs={6} className="butt" id="clear" onClick={handleAC}>AC</Col>
        <Col xs={3} className="butt" id="divide" onClick={chainCalculation ? () => continueOrRestart("/") : () => handleOperator("/")}>/</Col>
        <Col xs={3} className="butt" id="multiply" onClick={chainCalculation ? () => continueOrRestart("*") : () => handleOperator("*")}>*</Col>
      </Row>
      
      <Row className="El">
        <Col xs={3} className="butt" id="seven" onClick={chainCalculation ? () => continueOrRestart("7") : () => handleNumber("7")}>7</Col>
        <Col xs={3} className="butt" id="eight" onClick={chainCalculation ? () => continueOrRestart("8") : () => handleNumber("8")}>8</Col>
        <Col xs={3} className="butt" id="nine" onClick={chainCalculation ? () => continueOrRestart("9") : () => handleNumber("9")}>9</Col>
        <Col xs={3} className="butt" id="subtract" onClick={chainCalculation ? () => continueOrRestart("-") : () => handleMinus("-")}>-</Col>
      </Row>

      <Row className="El">
        <Col xs={3} className="butt" id="four" onClick={chainCalculation ? () => continueOrRestart("4") : () => handleNumber("4")}>4</Col>
        <Col xs={3} className="butt" id="five" onClick={chainCalculation ? () => continueOrRestart("5") : () => handleNumber("5")}>5</Col>
        <Col xs={3} className="butt" id="six" onClick={chainCalculation ? () => continueOrRestart("6") : () => handleNumber("6")}>6</Col>
        <Col xs={3} className="butt" id="add" onClick={chainCalculation ? () => continueOrRestart("+") : () => handleOperator("+")}>+</Col>
      </Row>

      <Row className="Dl">
        <Col xs={9}>
          <Row className="Hl">
            <Col xs={3} className="butt" id="one" onClick={chainCalculation ? () => continueOrRestart("1") : () => handleNumber("1")}>1</Col>
            <Col xs={3} className="butt" id="two" onClick={chainCalculation ? () => continueOrRestart("2") : () => handleNumber("2")}>2</Col>
            <Col xs={3} className="butt" id="three" onClick={chainCalculation ? () => continueOrRestart("3") : () => handleNumber("3")}>3</Col>
          </Row>
          <Row className="Hl">
            <Col xs={6} className="butt" id="zero" onClick={chainCalculation ? () => continueOrRestart("0") : () => handleNumber("0")}>0</Col>
            <Col xs={3} className="butt" id="decimal" onClick={chainCalculation ? () => continueOrRestart(".") : () => handleDot(".")}>.</Col>
          </Row>
        </Col>
        <Col xs={3} className="butt" id="equals" onClick={evaluate}>=</Col>
      </Row>

      </Container>
    </div>
  );
}

export default App;
